@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Roboto:100,300,400,500,700,900&display=swap&subset=korean);
@font-face {
    font-family: Samsung;
    src: url(/static/media/samsungsharpsans-bold.52e5256e.otf);
}

html {
    font-family: -apple-system, BlinkMacSystemFont, "Noto Sans KR", "Roboto", "Helvetica Neue", sans-serif;
    font-size: 10px;
    font-weight: 700;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
    background-color: whitesmoke;
}

body {
    margin: 0;
}
.appBar{flex-grow:1}.appBar .appBarBackground{background:transparent;position:fixed;box-shadow:none;filter:brightness(95%);-webkit-filter:brightness(95%);backdrop-filter:blur(8px);-webkit-backdrop-filter:blur(8px)}.appBar .appBarBackground .title{flex-grow:1;position:flex;font-family:Samsung;font-size:2rem;font-weight:900}.appBar .appBarBackground .title .link{text-decoration:none}

.background{background:url(/static/media/background2.7c54f8a7.jpg);background-position:center;background-size:cover;background-repeat:no-repeat;position:absolute;height:100%;width:100%;top:0;left:0;display:flex;flex-direction:column;align-items:center}.background .title{color:white;text-align:center;position:relative;top:30rem;font-size:8rem;font-family:Samsung}.background .subtitle{color:white;position:relative;top:35rem;text-align:center;font-size:1.8rem;font-weight:400}

@media screen and (max-width: 767px) {
  #music {display: none;}
}

@media screen and (min-width: 768px) {
  #music {display: block;}
}
.header{padding-top:8rem;padding-bottom:3rem}.header .title{font-size:2.5rem;font-weight:900}.header .subtitle{font-size:1.7rem;font-weight:500;color:slategrey}.header .btnAlign{display:flex;flex-direction:column;align-items:flex-end}.header .btnAlign .mediaBtn{-webkit-transition-duration:0.8s;transition-duration:0.8s;text-align:right;font-size:1.7rem;font-weight:700;color:white;margin-bottom:1rem;width:6.5rem;height:4rem;-webkit-transition:0.5s;transition:0.5s}.header .btnAlign .mediaBtn:hover{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:0.5s;transition:0.5s}.header .btnAlign #music{background-color:#018DFF}.header .btnAlign #movie{background-color:#f44}.header .btnAlign #book{background-color:#1ABF80}.header .btnAlign .logoutBtn{font-size:1.7rem;font-weight:700;margin-bottom:1rem;background-color:white;-webkit-transition:0.5s;transition:0.5s}.header .btnAlign .logoutBtn:hover{background-color:white;-webkit-transform:scale(1.06);transform:scale(1.06);-webkit-transition:0.5;transition:0.5}.header .divider{margin-top:1rem}.form .searchBar{margin:1.5rem 0rem 1.5rem 0rem;padding-left:1rem;position:center;display:flex;align-items:center;width:auto;-webkit-transition:0.5s;transition:0.5s}.form .searchBar:hover{-webkit-transform:scale(1.01);transform:scale(1.01);-webkit-transition:0.5s;transition:0.5s}.form .searchBar .input{flex:1 1;padding:0.5rem;font-size:1.7rem;font-weight:500}.form .searchBar .iconButton{padding:1rem}.filter{margin:2rem 0rem 3rem 0rem;padding:1.5rem;display:flex;flex-direction:column;align-items:center;font-size:1.7rem;font-weight:500}.filter .category{display:relative;font-weight:300}.filter .category .menuItem{font-size:1.7rem;font-weight:500}.filter #music{color:#018DFF}.filter #movie{color:#f44}.filter #book{color:#1ABF80}

.table{margin-top:0.5rem;width:100%}.table .tableContainer{height:70rem}.table .tableContainer .tableData{font-size:1.7rem;font-weight:400}.table .tableContainer .tableData .title{font-size:1.7rem;font-weight:700}.table .tableContainer .tableData .musicLikes{background-color:#018DFF;font-size:1.4rem;color:white;padding:0rem 1rem 0rem 1rem}.table .tableContainer .tableData .btnAlign{display:flex;flex-direction:column;align-items:flex-end}.table .tableContainer .tableData .btnAlign .deletion{font-size:1.7rem;font-weight:400}.table .tableContainer #ranking{text-align:center;padding:0rem 0rem 0rem 1rem}.table .tableContainer #category{min-width:12rem}.table .tableContainer .tableData2{font-size:1.7rem;font-weight:400;max-width:10rem}

.movie{background:white;display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;box-shadow:0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);font-size:1.7rem;padding-right:1rem;padding-bottom:1.5rem;-webkit-transition:0.5s;transition:0.5s}.movie:hover{-webkit-transform:scale(1.03);transform:scale(1.03);-webkit-transition:0.5s;transition:0.5s}.movie .moviePosterAlign{display:flex;flex-direction:column;align-items:center}.movie .moviePosterAlign .moviePoster{width:80%;height:auto;max-width:15rem;box-shadow:0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);margin-top:-1.5rem}.movie .moviePosterAlign .movieLikes{font-size:1.4rem;color:white;padding:0rem 1rem 0rem 1rem;margin-top:1.2rem;background:#f44}.movie .movieTitle{font-size:2.2rem;font-weight:700;margin-top:1rem}.movie .movieTitle .movieYear{margin-left:0.5rem;font-size:1.7rem;font-weight:500;color:grey}.movie .movieInfo{font-size:1.5rem;font-weight:400;color:slategrey;margin-top:0.7rem}.movie .movieCategory{font-size:1.6rem;font-weight:400;margin-top:0.7rem;margin-bottom:0.7rem}

.book{background:white;display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;box-shadow:0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);font-size:1.7rem;padding-right:1rem;padding-bottom:1.5rem;-webkit-transition:0.5s;transition:0.5s}.book:hover{-webkit-transform:scale(1.03);transform:scale(1.03);-webkit-transition:0.5s;transition:0.5s}.book .bookCoverAlign{display:flex;flex-direction:column;align-items:center}.book .bookCoverAlign .bookCover{width:80%;height:auto;max-width:15rem;box-shadow:0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);margin-top:-1.5rem}.book .bookCoverAlign .bookLikes{font-size:1.4rem;color:white;padding:0rem 1rem 0rem 1rem;margin-top:1.2rem;background:#1ABF80}.book .bookTitle{font-size:2.2rem;font-weight:700;margin-top:1rem}.book .bookInfo{font-size:1.5rem;font-weight:400;color:slategrey;margin-top:0.7rem}.book .bookCategory{font-size:1.6rem;font-weight:400;margin-top:0.7rem;margin-bottom:0.7rem}.book .bookDesc{font-size:1.5rem;font-weight:400;margin:1rem;padding-left:1rem}

.card{margin-top:2rem;font-size:1.7rem;padding-left:1.5rem;padding-right:1.5rem;padding-bottom:3rem}.card .formTitle{font-size:2.2rem;font-weight:70rem;margin:1.5rem;text-align:center}.card .form{display:flex;flex-direction:column;align-items:center;margin-top:2rem;margin-bottom:2rem}.card .form .naverSearch{margin:2rem;position:center;display:flex;align-items:center;width:auto;-webkit-transition:0.5s;transition:0.5s}.card .form .naverSearch:hover{-webkit-transform:scale(1.05);transform:scale(1.05);-webkit-transition:0.5s;transition:0.5s}.card .form .naverSearch .input{margin-left:1rem;padding:0.5rem;flex:1 1;font-size:2rem;font-weight:700}.card .guide{text-align:center;font-size:1.7rem;font-weight:400}.card .guide .menuItem{font-size:1.7rem;font-weight:500}.card .submitBtn{margin-top:3rem;font-size:2rem;color:white}.card #music{background-color:#018DFF;-webkit-transition:0.7s;transition:0.7s}.card #music:hover{background-color:#018DFF;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:0.7s;transition:0.7s}.card #movie{background-color:#f44;-webkit-transition:0.7s;transition:0.7s}.card #movie:hover{background-color:#f44;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:0.7s;transition:0.7s}.card #book{background-color:#1ABF80;-webkit-transition:0.7s;transition:0.7s}.card #book:hover{background-color:#1ABF80;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:0.7s;transition:0.7s}.card .menuItem{font-size:1.7rem;font-weight:500}

.loginCard{margin-top:2rem;font-size:1.7rem;padding-left:1.5rem;padding-right:1.5rem}.loginCard .subtitle{font-size:2.2rem;font-weight:700;text-align:center;margin:1.5rem 1.5rem}.loginCard .loginForm{display:flex;flex-direction:column;align-items:center;margin-top:1.5rem;margin-bottom:3rem}.loginCard .loginForm .submitButton{background:transparent;font-size:2rem;color:white;margin-top:3rem;-webkit-transition:0.5s;transition:0.5s}.loginCard .loginForm .submitButton:hover{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:0.5s;transition:0.5s}.loginCard .loginForm #login{background:linear-gradient(45deg, #2196F3 30%, #21CBF3 90%);box-shadow:0 3px 5px 2px rgba(33,203,243,0.3)}.loginCard .loginForm #register{background:linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);box-shadow:0 3px 5px 2px rgba(255,105,135,0.3)}

.myPage{font-size:1.7rem;margin-top:10rem;padding:0rem 1rem 3rem 1rem}.myPage .accountIconAlign{display:flex;flex-direction:column;align-items:center}.myPage .accountIconAlign .accountIcon{color:midnightblue;margin-top:-8rem;width:16rem;height:16rem}.myPage .userName{text-align:center;font-size:2.1rem;font-weight:900}.myPage .userDescription{text-align:center;font-size:1.8rem;font-weight:300}.myPage .btnGroup{padding:1.5rem;font-size:2.2rem;font-weight:700}.myPageSubtitle{font-size:2.2rem;font-weight:700;margin:1.5rem}#myPageMusicSub{color:#018DFF}#myPageMovieSub{color:#f44}#myPageBookSub{color:#1ABF80}

