@font-face {
    font-family: Samsung;
    src: url('./fonts/samsungsharpsans-bold.otf');
}

@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Roboto:100,300,400,500,700,900&display=swap&subset=korean');

html {
    font-family: -apple-system, BlinkMacSystemFont, "Noto Sans KR", "Roboto", "Helvetica Neue", sans-serif;
    font-size: 10px;
    font-weight: 700;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
    background-color: whitesmoke;
}

body {
    margin: 0;
}